@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}
:root {
    --black: #333;
    --black2: #333;
    --white: #fff;
    --white2: #fff;
}

body {
    overflow-x: hidden;
}

.dark {
    --black: #fff;
    --black2: #444;
    --white: #333;
    --white2: #444;
}

/* STYLE NAVBAR themeSwitch ' NIGHT AND DAY MODE ' */



.themeSwitch::before {
    content: '\f186';
    font-family: fontAwesome;
     
}

.dark .themeSwitch::before {
    content: '\f185';
    font-family: fontAwesome;
     
}





section {
    position: relative;
    min-height: 100vh;
}

.banner {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 20px;
}

.banner .content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.banner .imgBx {
    position: relative;
    height: 260px;
    width: 260px;
    overflow: hidden;
    border-radius: 50%;
    margin-bottom: 20px;
}

.banner .imgBx img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.banner .content #cvBtn {

}
h3 {
    position: relative;
    color: var(--black);
    text-transform: uppercase;
    font-size: 1.2rem;
    letter-spacing: 2px;
}

p {
    color: #777;
    text-align: center;
}

.dark p {
    color: var(--black);
}

.btn {
    position: relative;
    display: inline-block;
    background: var(--black2);
    color: #fff;
    padding: 10px 30px;
    font-weight: 400;
    text-decoration: none;
    letter-spacing: 1px;
    margin-top: 10px;
}

.btn:hover {
    background: rgba(0,0,0,0.09);
}

.dark .btn:hover {
    background: #2196f3;
    color: #fff;
}

.socialMedia {
    position: absolute;
    bottom: 20px;
    margin: 0;
    padding: 0;
    display: flex;
}

.socialMedia li {
    list-style: none;
}

.socialMedia li a {
    display: inline-block;
    width: 40px;
    height: 40px;
    background: rgba(0,0,0,0.05);
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: var(--black);
    font-size: 1.2rem;
}

.socialMedia li a:hover {
    background: var(--black2);
    color: #fff;
}

.dark .socialMedia li a:hover {
    background: #2196f3;
}
.adjust {
  position: relative;
  min-height: 100vh;
  width: 100%;
  padding: 0 40px;
  padding-top: 80px;
  background: var(--white);
  color: var(--black);
  display: inline-block;
}

.title {
  width: 100%;
  text-align: center;
}

.title h2 {
  margin-bottom: 30px;
  position: relative;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.2em;
}

.title h2::before {
  content: "";
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: -10px;
  width: 50px;
  height: 1px;
  background: var(--black);
}

about .content {
  display: flex;
  margin-top: 40px;
}

.about .content .textBox {
  width: 100%;
  min-width: 100%;
}

@media (max-width: 922px) {
  .adjust {
    padding: 80px 20px 0;
  }

  .about .content {
    flex-direction: column;
  }

  .about .content .textBox {
    width: 100%;
    min-width: 100%;
  }

  .about .content .imgBx {
    margin: 20px 0 0;
    display: flex;
    justify-content: center;
  }
}

.service .content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  margin: 40px 0;
}

.service .content .serviceBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgba(0, 0, 0, 0.05);
  padding: 20px 10px;
  background: var(--white2);
}

.dark .service .content .serviceBox:hover {
  background: #2196f3;
}

.service .content .serviceBox img {
  max-width: 50px;
}

.dark .service .content .serviceBox img {
  -webkit-filter: invert(1);
          filter: invert(1);
}

.service .content .serviceBox h2 {
  font-size: 1.1em;
  margin: 20px 0 10px;
  font-weight: 600;
  color: var(--black);
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.service .content .serviceBox p {
  text-align: center;
}

@media (max-width: 922px) {
  .service .content {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .service .content {
    grid-template-columns: repeat(1, 1fr);
  }
}

.project .content {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  margin: 40px;
}

.project .content .workBx {
  position: relative;
  width: 100%;
  height: 300px;
  overflow: hidden;
}

.project .content .workBx .imgBx {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-transform: translateY(0);
          transform: translateY(0);
  transition: 0.5s ease-in-out;
}

.project .content .workBx:hover .imgBx {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.project .content .workBx .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project .content .workBx .textBx {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--black);
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  transition: 0.5s ease-in-out;
}

.dark .project .content .workBx .textBx {
  background: #444;
}

.project .content .workBx:hover .textBx {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.project .content .workBx .textBx h3 {
  color: #fff;
  font-size: 1em;
}

@media (max-width: 922px) {
  .project .content {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 767px) {
  .project .content {
    grid-template-columns: repeat(1, 1fr);
  }
}

.contact {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.contact .contactForm {
  position: relative;
  width: 100%;
  max-width: 700px;
}

.contact .contactForm .row1 {
  width: 100%;
  margin-top: 40px;
  display: flex;
}

.contact .contactForm .row1 {
  width: 100%;
  padding: 10px;
  border: none;
  outline: none;
  color: var(--black);
  font-size: 16px;
}

.contact .contactForm .row1 .info {
  margin-left: 16px;
}

.contact .contactForm .row1 .info h6 {
  color: cornflowerblue;
  font-weight: bolder;
}

.contact .contactForm .row1 .info span {
  font-size: medium;
  color: var(--black);
}

.contact .contactForm .row1 .info span a {
  text-decoration: none;
  color: var(--black);
}

@media (max-width: 767px) {
  .contact {
    margin-bottom: 100px;
  }

  .contact .contactForm .row1 {
    grid-template-columns: repeat(1, 1fr);
  }
}

.navbar {
  position: relative;
  width: 100%;
}

.navigation {
  position: fixed;
  top: 0;
  height: 100%;
  width: 300px;
  border-right: 1px solid rgba(0, 0, 0, 0.05);
  background: var(--white2);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  transition: width 0.5s, left 0.5s;
}

.dark .navigation {
  border-right: 1px solid rgba(0, 0, 0, 0.5);
}

.navigation.active {
  width: 0;
}

.navigation ul {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  padding: 20px 40px;
}

.navigation ul li {
  position: relative;
  width: 100%;
  list-style: none;
}

.navigation ul li a {
  position: relative;
  display: block;
  width: 100%;
  margin: 10px 0;
  white-space: normal;
  text-transform: uppercase;
  display: flex;
  color: var(--black);
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 2px;
  opacity: 0.5;
}

.navigation ul li a:hover {
  opacity: 1;
}

.content h3,
.content p {
  text-align: center;
  line-height: 33px;
  letter-spacing: 1px;
}

.main {
  position: absolute;
  background: var(--white);
  width: calc(100% - 300px);
  left: 300px;
  top: 0;
  min-height: 100vh;
  transition: width 0.5s, left 0.5s;
}
.main.active {
  width: calc(100% - 0px);
  left: 0;
}

.main .topbar {
  position: fixed;
  top: 0;
  width: calc(100vw - 300px);
  left: 300px;
  background: var(--white2);
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  height: 60px;
  padding: 0 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 10000;
  transition: width 0.5s, left 0.5s;
}

.dark .main .topbar {
  border-bottom: 1px solid rgba(0, 0, 0, 0.5);
}

.main .topbar.active {
  width: calc(100vw - 0px);
  left: 0;
}

.main .topbar .logo {
  color: var(--black);
  font-weight: 700;
  text-decoration: none;
  font-size: 1.4em;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.main .topbar .toggle {
  position: relative;
  height: 60px;
  width: 60px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  color: var(--black);
}

.themeSwitch {
  position: absolute;
  bottom: 20px;
  left: 40px;
  background: var(--black);
  color: var(--white);
  height: 30px;
  width: 30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.themeSwitch::after {
  content: "Switch To Night Mode";
  position: absolute;
  left: 40px;
  font-size: 0.7em;
  color: var(--black);
  white-space: nowrap;
  font-weight: 300;
  letter-spacing: 1px;
  pointer-events: none;
}

.dark .themeSwitch::after {
  content: "Switch To Light Mode";
}

@media (max-width: 922px) {
  .navigation {
    left: -100%;
  }

  .navigation.active {
    left: 0;
    width: 100%;
  }

  .main {
    width: 100%;
    left: 0;
  }

  .main.active {
    left: 100%;
  }

  .main .topbar {
    width: 100%;
    left: 0;
    padding: 0 20px;
  }

  .navigation ul {
    padding: 20px 20px;
  }

  .banner .imgBx {
    position: relative;
    height: 220px;
    width: 220px;
  }

  .content h3,
  .content p {
    text-align: center;
  }
}

@media (max-width: 450px) {
  .navigation {
    height: calc(100vh - 100px);
  }
}

